* {
    box-sizing: border-box;
  }
  
  body {
    margin: 0;
    background: #3b3b3b;
  }
  
  button {
    background: transparent;
    border: 0;
    padding: 0;
    cursor: pointer;
  }
  
  .sidebar {
    position: fixed;
    overflow: hidden;
    top: 0;
    left: 0;
    width: 60px;
    height: 100%;
    background: #2c2c2c;
    transition: width 0.4s;
    z-index: 1000;  /* This value should be higher than the z-index of any other overlapping component */
  }
  
  .sidebar.open {
    width: 260px;
  }
  
  .sidebar-inner {
    position: absolute;
    top: 0;
    left: 0;
    width: 260px;
  }
  
  .sidebar-header {
    display: flex;
    align-items: center;
    height: 72px;
    background: rgb(0 0 0 / 15%);
  }
  
  .sidebar-burger {
    width: 60px;
    height: 72px;
    display: grid;
    place-items: center;
    color: #f9f9f9;
  }
  
  .sidebar-logo {
    height: 28px;
  }
  
  .sidebar-menu {
    display: grid;
    padding: 10px;
  }
  
  .sidebar-button {
    display: flex;
    gap: 16px;
    align-items: center;
    height: 56px;
    width: 100%;
    font-family: 'Roboto Condensed', sans-serif;
    font-size: 17px;
    text-transform: capitalize;
    line-height: 1;
    padding: 0 10px;
    border-radius: 8px;
    color: #f9f9f9;
    opacity: 0.8;
  }
  
  .sidebar-button:hover {
    background: rgb(0 0 0 / 30%);
    opacity: 1;
  }
  
  .sidebar-button:hover > span {
    opacity: 1;
  }
  
  .sidebar-logo,
  .sidebar-button p {
    opacity: 0;
    transition: 0.3s;
  }
  
  .sidebar.open :is(.sidebar-button p, .sidebar-logo) {
    opacity: 1;
  }
  
  .sidebar-button > img {
    width: 24px;
    height: 24px;
  }
  
  .sidebar-button > span {
    opacity: 0.5;
  }