
.global-container2 {
    padding: 20px;
    border-radius: 8px;
    margin-top: 20px;
    margin-left: 200px;
    margin-right: auto;
    background-color: rgb(185, 185, 185);
    display: flex;
    flex-direction: row; /* Horizontal layout */
    align-items: flex-start; /* Align children to the top */
    gap: 20px;
    width: 95%;
    max-width: 1600px;
    overflow: hidden;
    transition: all 4s ease-in-out;
}
/* Inputs on the left */
.input-group2 {
    flex: none;
    width: 230px;
}

.input-group2 label {
    display: block;
    margin-bottom: 5px;
    color: #333;
    font-weight: bold;
    font-size: 16px;
    font-family: 'Roboto Condensed', sans-serif;
}
.input-group2 input {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    text-align: left;
    color: #333;
    font-weight: bold;
    font-size: 16px;
    font-family: 'Roboto Condensed', sans-serif;
}

.chart-container2{
    flex: 1;
    height: 460px;
    padding: 10px;
    padding-left: 0px;
    background-color: #f6f6f6;
    min-width: 0;  /* Allow the container to shrink without restriction */
    border-radius: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: visible;
}

.right-container2 {
    flex-grow: 1; /* this will allow the right container to grow and take up the remaining space */
    /* Optionally, you may set a margin or padding if you want some space around it */
    margin-left: 10px; /* adjust as needed */
    padding: 10px; /* adjust as needed */
    /* Set the maximum and minimum sizes if you want to control the resizing */
    min-width: 0; /* for flex items to shrink below their content's intrinsic size */
    max-width: 100%; /* optional, to prevent it from growing beyond the container size */
  }
  

.metrics-container2 {
    display: flex;
    flex-direction: row;
    gap: 10px;
    padding: 20px;
    background: #f2f2f2;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
    margin-bottom: 20px;
    margin-top: 20px;
  }
  
  .metrics-container2 > div {
    font-size: 16px;
    color: #333;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
    background: white;
  }
  
  .offer-value {
    font-weight: bold;
    color: #4CAF50;
  }
  
  .monthly-payment {
    font-weight: bold;
    color: #2196F3;
  }
  
  .acquisition-tax {
    font-weight: bold;
    color: #F44336;
  }

  .loan-amount {
    font-weight: bold;
    color: #F44336;
  }

.table-container {
  display: none;
}
  