/* Global reset */
* {
    box-sizing: border-box;
}

body {
    background-color: #1f1f1f;  /* Black background */
    color: #000000;  /* Set text color to white for better visibility on a black background */
}

/* Main container */
.global-container {
    padding: 20px;
    border-radius: 8px;
    margin-top: auto;
    margin-left: auto;
    margin-right: auto;
    background-color: rgb(185, 185, 185);
    display: flex;
    flex-direction: row; /* Horizontal layout */
    align-items: flex-start; /* Align children to the top */
    gap: 20px;
    width: 95%;
    max-width: 1600px;
    overflow: hidden;
    transition: all 4s ease-in-out;
}

.global-container-small {
    width: fit-content;
}

@media (max-width: 768px) { /* This breakpoint can be adjusted as per your needs */
    .global-container-small {
        width: 95vw;
    }
}

@media (max-width: 768px) { /* This breakpoint can be adjusted as per your needs */
    .global-container {
        margin-top: auto;
        flex-direction: column;
    }
}


/* Inputs on the left */
.input-group {
    flex: none;
    width: 230px;
}

.input-group label {
    display: block;
    margin-bottom: 5px;
    color: #333;
    font-weight: bold;
    font-size: 16px;
    font-family: 'Roboto Condensed', sans-serif;
}

@media (max-width: 768px) { /* This breakpoint can be adjusted as per your needs */
    .input-group label {
        font-size: 12px;
    }
}

.input-group input {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    text-align: left;
    color: #333;
    font-weight: bold;
    font-size: 16px;
    font-family: 'Roboto Condensed', sans-serif;
}

@media (max-width: 768px) { /* This breakpoint can be adjusted as per your needs */
    .input-group input {
        font-size: 10px;
    }
}

.input-group input[type="range"] {
    margin-top: -10px;
}

@media (max-width: 768px) { /* This breakpoint can be adjusted as per your needs */
    .input-group input[type="range"] {
        display: none;
    }
}

.input-group button {
    display: block;
    max-width: 250px;
    width: 100%;
    margin-top: 10px;
    padding: 10px;
    background-color: #ff9900;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: bold;
    font-size: 16px;
}

.input-group button:hover {
    background-color: #c07300;
}

@media (max-width: 768px) { /* This breakpoint can be adjusted as per your needs */
    .input-group {
        margin: auto;
        width: 100%;
    }
}

@media (max-width: 768px) { /* This breakpoint can be adjusted as per your needs */
    .input-group button {
        margin: auto;
    }
}

/* Container for all charts */
.charts-content {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 20px;
    flex: 1;
    min-width: 0;
}

/* Individual chart containers */
.top-charts {
    display: flex;
    flex: 1;
    gap: 20px;
    width: 100%;
}

@media (max-width: 768px) {
    .top-charts {
        flex-direction: column;
        height: 500px;
    }
}


.bottom-charts {
    display: flex;
    flex: 1;
    gap: 20px;
    width: 100%;
}

.pie-chart-container {
    flex: 1 1 33%;  /* This will grow to 1/3 and shrink from 1/3 */
    min-height: 300px;  
    max-height: 400px;  
    padding: 10px;
    padding-left: 0px;
    background-color: #f6f6f6;
    min-width: 0;  /* Allow the container to shrink without restriction */
    border-radius: 8px;
}

.area-chart-container {
    flex: 2 2 66%;  /* This will grow to 2/3 and shrink from 2/3 */
    min-height: 300px; 
    max-height: 400px;  
    padding: 10px;
    padding-left: 0px;
    background-color: #f6f6f6;
    min-width: 0;  /* Allow the container to shrink without restriction */
    border-radius: 8px;
}

.line-chart-container{
    flex: 1;
    height: 460px;
    padding: 10px;
    padding-left: 0px;
    background-color: #f6f6f6;
    min-width: 0;  /* Allow the container to shrink without restriction */
    border-radius: 8px;
}


.financial_metrics {
    display: flex; /* Use flexbox for layout */
    justify-content: space-between; /* Distributes the containers evenly */
    align-items: start; /* Aligns containers to the top */
    gap: 20px; /* Adjust the gap between containers */
    height: 50px;
}

.container {
    border: 1px solid #e0e0e0; /* Add a border to see the containers */
    background-color: #f6f6f6;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Optional shadow for aesthetics */
    flex: 1; /* Makes sure all containers take up equal width */
    border-radius: 8px;
    height: 70px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-size: 16px;
    font-family: 'Roboto Condensed', sans-serif;
}

@media (max-width: 768px) { /* This breakpoint can be adjusted as per your needs */
    .financial_metrics {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        height: auto;
        width: 100%;
    }
}

@media (max-width: 768px) { /* This breakpoint can be adjusted as per your needs */
    .container {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        height: auto;
        width: 100%;
    }
}
